"use client"

import { useState, useEffect } from 'react'
import { BOOKING_URL } from '@/lib/constants'

export function useBookingUrl() {
  const [url, setUrl] = useState(BOOKING_URL)

  useEffect(() => {
    // Only run on client
    if (typeof window !== 'undefined') {
      try {
        const timezone = Intl.DateTimeFormat().resolvedOptions().timeZone
        if (timezone) {
          // Check if URL already has query params
          const separator = BOOKING_URL.includes('?') ? '&' : '?'
          setUrl(`${BOOKING_URL}${separator}timezone=${encodeURIComponent(timezone)}`)
        }
      } catch (e) {
        console.warn("Failed to detect timezone", e)
      }
    }
  }, [])

  return url
}
