'use client'

export default function myImageLoader({ src, width, quality: _quality }: { src: string; width: number; quality?: number }) {
  const isRemote = src.startsWith('http') || src.startsWith('//');
  if (isRemote) {
    return `${src}?w=${width}`;
  }

  // If in development, use original to avoid needing to run optimization script constantly
  if (process.env.NODE_ENV === 'development') {
    return `${src}?w=${width}`;
  }

  // In production, use the pre-generated optimized WebP
  // src is like "/assets/images/Aura.png"
  // We want "/optimized/assets/images/Aura.webp"
  
  const fileNameWithExt = src.split('/').pop() || '';
  const directory = src.substring(0, src.lastIndexOf(fileNameWithExt)); // e.g. "/assets/images/"
  const fileName = fileNameWithExt.substring(0, fileNameWithExt.lastIndexOf('.')); // "Aura"

  return `/optimized${directory}${fileName}.webp?w=${width}`;
}
